/**/

new Vue({
    el: '#content',
    data:
    {
        numcard:num_card_data,
        accountdata: formatAccount(commonjs.account),
        shopinfo:shop_info,
        erlist:er_list,
        window:0,
        /*region data*/
        regionname: selectedRegionName,
        showingregion: showingRegion,
        /**/
        testdata:"#FF00000",
    },
    mounted:function()
    {
        //update every rack
        setInterval(function(){

            //问题： 机房温度应该与卡的平均温度有关，而与速度无关
            for(var region in regions[0]) // 更新每个区域的温度
            {
                var heatsum = 0;
                var nettempspeed = 0.0;
                var avgtemp = 0;
                var usedspace = 0;
                var regioncapacity = 0;
                for(var i=0; i<regions[0][region].racks.length; ++i)
                {
                    usedspace += regions[0][region].racks[i].occupy;
                    regioncapacity += regions[0][region].racks[i].capacity;
                    for(var j=0; j<regions[0][region].racks[i].items.length; ++j)
                    {
                        var thiscard = regions[0][region].racks[i].items[j];
                        if(thiscard.type == 'gcard')
                        {
                            thiscard.temp = Math.min(Math.max(thiscard.temp + thiscard.heat - showingRegion[0].cooler, showingRegion[0].mintemp),thiscard.maxheat);
                            heatsum += thiscard.heat;
                            avgtemp +=thiscard.temp;
                        }
                        else if(thiscard.type == 'radiator')
                        {
                            //cool += thiscard.effect;
                        }
                    }
                }
                if(regioncapacity != 0)var usedspacerate = usedspace / regioncapacity;
                if(regions[0][region].gcardnum!=0)
                {
                    nettempspeed = heatsum - showingRegion[0].cooler;
                    avgtemp /= regions[0][region].gcardnum;
                }

                //nettempspeed = addNatureSpeed(regions[0][region].basetemp, regions[0][region].tempture, nettempspeed);
                //console.log(nettempspeed);
                //regions[0][region].tempture = Math.min(Math.max(regions[0][region].tempture + nettempspeed, showingRegion[0].mintemp),100);
                regions[0][region].tempture = Math.min(Math.max(avgtemp * usedspacerate, showingRegion[0].mintemp),100);
                //console.log(avgtemp);
            }
        },1);
    },
    methods:
    {


        addcard:function(card)
        {
            if(commonjs.account.cash.amount >= card.prize)
            {
                //新建一个显卡的对象，而不是原来的引用
                var newcard = getnewCard(card);
                if(!addcardInRack(newcard))
                {
                    if(newcard.powercs)showingRegion[0].powercs += newcard.powercs;
                    if(newcard.type == 'gcard')
                    {
                        updataAccount({cash:-newcard.prize});
                        updataIncrease({bitcoin:newcard.speed});
                        ++num_card_data.num;
                        //showingRegion[0].gcardnum++;
                        regions[0][selectedRegionName].gcardnum++;
                    }
                    else if(newcard.type == 'radiator')
                    {
                        ++num_card_data.radiatornum;
                        showingRegion[0].cooler += newcard.effect;
                        showingRegion[0].mintemp = (showingRegion[0].mintemp * (num_card_data.radiatornum-1) + newcard.mincool)/num_card_data.radiatornum;
                    }
                    else if(newcard.type == 'generator')
                    {
                        ++num_card_data.generatornum;
                        showingRegion[0].powersupport += newcard.power;
                    }
                }
            }
        },
        mousework:function()
        {
            var addcash = {cash: 10000};
            updataAccount(addcash);
        },
        btc2Cash:function()
        {
            var money = commonjs.account.bitcoin.amount * .2 * exrate.bitcoin;
            updataAccount({bitcoin: -commonjs.account.bitcoin.amount, cash: money});
        },
        removecard:function(indexcard,card, rack)
        {
            //
            rack.items.splice(indexcard,1);
            rack.occupy -= card.space;         //要换成space
            removecardInRack(card);
            if(card.powercs)showingRegion[0].powercs -= card.powercs;
            if(card.type == 'gcard')
            {
                showingRegion[0].gcardnum--;
                --num_card_data.num;
            }
            else if(card.type == 'radiator')
            {
                --num_card_data.radiatornum;
                showingRegion[0].cooler -= card.effect;
                if(num_card_data.num <= 0)showingRegion[0].cooler = 0;//showingRegion[0].basetemp;
                else 
                {
                    showingRegion[0].mintemp = (showingRegion[0].mintemp * (num_card_data.num+1) - card.mincool)/num_card_data.num;
                }
            }
            else if(card.type == 'generator')
            {
                --num_card_data.generatornum;
                showingRegion[0].powersupport -= card.power;
            }
        },
        changeregion:function()
        {
            selectedRegionName = commonjs.selectedRegion;
            if(regions[0][selectedRegionName])
                showingRegion[0] = regions[0][selectedRegionName];
            this.regionname = selectedRegionName;
        },

    }
})


